  font-weight: 600;
}
@media (max-width: 767px) {
  .contacts__title {
    margin-bottom: 20px;
    font-size: 30px;
    line-height: 30px;
  }
}

.contacts__wrapper {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  gap: 40px;
}
@media (max-width: 1229px) {
  .contacts__wrapper {
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
  }
}

.contacts__photo {
  width: 500px;
  height: 600px;
}
.contacts__photo img {
  object-fit: cover;
  width: 500px;
  height: 600px;
}
@media (max-width: 767px) {
  .contacts__photo {
    width: 90%;
    margin: 0 auto;
  }
  .contacts__photo img {
    display: block;
    margin: 0 auto;
  }
}

.contacts__list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
  flex-direction: column;
  gap: 25px;
  min-width: 225px;
}
@media (max-width: 767px) {
  .contacts__list {
    gap: 12px;
  }
}

.contacts__link {
  line-height: 23px;
  display: flex;
  justify-content: flex-start;
  gap: 15px;
  align-items: center;
  height: 100%;
  width: 100%;
  transition: 0.3s ease;
  outline: none;
}
.contacts__link svg {
  fill: #1e1e1e;
}
@media (hover: hover) {
  .contacts__link:hover:not(.focus-visible) {
    opacity: 0.7;
    outline: none;
  }
}
.contacts__link.focus-visible:focus {
  opacity: 0.7;
  outline: none;
}

.contacts__link--phone svg {
  stroke: #1e1e1e;
}

.contacts__list .contacts__link:active {
  opacity: 1;
}