.wrapper {
  position: relative;

  display: flex;
  flex-direction: row;
  user-select: none;
  min-height: 100vh;
  /* stylelint-disable-next-line */
  min-height: calc(100 * var(--vh, 1vh));

  @include vp-1229 {
    flex-direction: column;
  }

  .header,
  .footer {
    flex-shrink: 0;
  }

  main {
    flex-grow: 1;
  }
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(100%);
}

.js-focus-visible :focus:not(.focus-visible) {
  outline: none;
}

.scroll-lock {
  overflow: hidden;
}
